}

/* Hover */
.checkbox-google
    input[type='checkbox']:not(:disabled)
    + .checkbox-google-switch {
    cursor: pointer;
    border-color: rgba(0, 0, 0, 0.3);
}

/* Active/Focus */
.checkbox-google
    input[type='checkbox']:not(:disabled):active
    + .checkbox-google-switch:before,
.checkbox-google
